/* Desktop */

.grid-item-1 {
    background: red;
    width: 100%;
    height: auto;
    grid-area: grid-item-1;
}
.grid-item-2 {
    background: blue;
    width: 100%;
    height: auto;
    grid-area: grid-item-2;
}
.grid-item-3 {
    background: green;
    width: 100%;
    height: auto;
    grid-area: grid-item-3;
}
.grid-item-4 {
    background: yellow;
    width: 100%;
    height: auto;
    grid-area: grid-item-4;
}
.grid-item-5 {
    background: black;
    width: 100%;
    height: auto;
    grid-area: grid-item-5;
}
.grid-item-6 {
    background: orange;
    width: 100%;
    height: auto;
    grid-area: grid-item-6;
}
.grid-item-7 {
    background: teal;
    width: 100%;
    height: auto;
    grid-area: grid-item-7;
}
.grid-item-8 {
    background: gray;
    width: 100%;
    height: auto;
    grid-area: grid-item-8;
}
.grid-item-9 {
    background: aquamarine;
    width: 100%;
    height: auto;
    grid-area: grid-item-9;
}


@media all and (min-width: 1024px)  {
    .home {
        display: grid;
        grid-template-areas: 
        "grid-item-1 grid-item-1 grid-item-2"
        "grid-item-1 grid-item-1 grid-item-3"
        "grid-item-4 grid-item-6 grid-item-6"
        "grid-item-5 grid-item-6 grid-item-6"
        "grid-item-7 grid-item-8 grid-item-9";
        grid-row-gap: 2rem;
        grid-column-gap: 2rem;
        height: 100vh;
        margin: 0;
    }
 }

